import './index.scss';
import { useTranslation } from 'react-i18next';
import img from '../../../assets/images/pressure-systems.png';

function ProductsPressureSystems() {
  const { t } = useTranslation();

  return (
    <div className="products__pressure-systems">
      <div className="container">
        <div className="products__pressure-systems__inner d-flex">
          <div className="products__pressure-systems__left">
            <h1 className="pn-bold">{ t('products.pressureSystemsT') }</h1>
            <p className="text-md">{ t('products.pressureSystemsD') }</p>
          </div>
          <div className="products__pressure-systems__right">
            <img src={img} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductsPressureSystems;